/* Fondo general */
body {
  background-color: #121212; /* gris más oscuro */
  color: #fff;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
}

/* NAVBAR */
.navbar {
  background-color: #2e2e2e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  color: #f7c948;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #f7c948;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
}

.nav-links a {
  text-decoration: none;
  color: #f7c948;
  transition: 0.3s;
}

.nav-links a:hover {
  color: #ffffff;
}

/* Carrito */
.cart-widget {
  position: relative;
  font-size: 1.5rem;
}

.cart-count {
  position: absolute;
  top: -8px;
  right: -10px;
  background: #f7c948;
  color: #1c1c1c;
  font-size: 0.8rem;
  border-radius: 50%;
  padding: 2px 6px;
}

/* Contenido principal */

body {
  background-color: #1c1c1c; 
  color: #fff;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 85vh;
  text-align: center;
  gap: 1rem;
}

/* Títulos */
h1 {
  color: #f7c948;
  font-size: 3rem;
  letter-spacing: 2px;
}

h2 {
  color: #f7c948;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

/* Texto bienvenida */
.item-container p {
  color: #ffffff;
  font-size: 1.2rem;
}